import React from "react";
import { Link } from "react-router-dom";

const ListGroup = (props) => {
  const { theme } = props;
  const options = [
    { label: "", path: "/introduction" },
    { label: "", path: "/activities" },
    { label: "", path: "/my-concern" },
    { label: "", path: "/find-friends" },
  ];

  return theme === "daytime" ? (
    <div className=" list-group col-3">
      <div className="sticky-top" style={{ top: "1em" }}>
        <div className="rounded">
          {options.map((o) => (
            <Link
              key={o.label}
              className="list-group-item list-group-item-action bg-light"
              to={o.path}
            >
              {o.label}
            </Link>
          ))}
        </div>

        <div className="mt-3">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title"></h5>
              <h6 className="card-subtitle mb-2 text-muted"></h6>
              <p className="card-text">
                
              </p>
              <div className="text-center">
                <a
                  href="https://user.qzone.qq.com/1303140304/"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="card-link text-danger font-weight-bolder"
                >
                  qq
                </a>
                <a
                  href="https://github.com/fcater/React-bbs"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="card-link  text-danger font-weight-bolder"
                >
                  
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className="mt-3">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title text-muted"></h5>
              <h6 className="card-subtitle mt-2 text-muted"></h6>
              <p className="card-text"></p>
            </div>
          </div>
        </div>
      </div>
    </div>
  ) : (
    //
    <div className=" list-group col-3">
      <div className="sticky-top" style={{ top: "1em" }}>
        <div className="rounded">
          {options.map((o) => (
            <Link
              key={o.label}
              className="list-group-item list-group-item-action bg-dark text-light"
              to={o.path}
            >
              {o.label}
            </Link>
          ))}
        </div>

        <div className="mt-3">
          <div className="card bg-dark text-light">
            <div className="card-body ">
              <h5 className="card-title"></h5>
              <h6 className="card-subtitle mb-2 text-muted"></h6>
              <p className="card-text">
                
              </p>
              <div className="text-center">
                <a
                  href="https://user.qzone.qq.com/1303140304/"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="card-link text-danger font-weight-bolder"
                >
                  qq
                </a>
                <a
                  href="https://github.com/fcater/React-bbs"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="card-link  text-danger font-weight-bolder"
                >
                  
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className="mt-3">
          <div className="card bg-dark text-light">
            <div className="card-body">
              <h5 className="card-title text-muted"></h5>
              <h6 className="card-subtitle mt-2 text-muted"></h6>
              <p className="card-text"></p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ListGroup;
